import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {a1: 0,
                  a2: 0,
                  a3: 0,
                  a4: 0,
                  a5: 0,
                  b1: 0,
                  b2: 0,
                  b3: 0,
                  b4: 0,
                  b5: 0};
  }
  a1increment = () => {
    this.setState({a1: this.state.a1 + 1});
  }
  a1reset = () => {
    this.setState({a1: 0});
  }
  a2increment = () => {
    this.setState({a2: this.state.a2 + 1});
  }
  a2reset = () => {
    this.setState({a2: 0});
  }
  a3increment = () => {
    this.setState({a3: this.state.a3 + 1});
  }
  a3reset = () => {
    this.setState({a3: 0});
  }
  a4increment = () => {
    this.setState({a4: this.state.a4 + 1});
  }
  a4reset = () => {
    this.setState({a4: 0});
  }
  a5increment = () => {
    this.setState({a5: this.state.a5 + 1});
  }
  a5reset = () => {
    this.setState({a5: 0});
  }
  TeamAReset = () => {
    this.setState({a1: 0});
    this.setState({a2: 0});
    this.setState({a3: 0});
    this.setState({a4: 0});
    this.setState({a5: 0});
  }

  b1increment = () => {
    this.setState({b1: this.state.b1 + 1});
  }
  b1reset = () => {
    this.setState({b1: 0});
  }
  b2increment = () => {
    this.setState({b2: this.state.b2 + 1});
  }
  b2reset = () => {
    this.setState({b2: 0});
  }
  b3increment = () => {
    this.setState({b3: this.state.b3 + 1});
  }
  b3reset = () => {
    this.setState({b3: 0});
  }
  b4increment = () => {
    this.setState({b4: this.state.b4 + 1});
  }
  b4reset = () => {
    this.setState({b4: 0});
  }
  b5increment = () => {
    this.setState({b5: this.state.b5 + 1});
  }
  b5reset = () => {
    this.setState({b5: 0});
  }
  TeamBReset = () => {
    this.setState({b1: 0});
    this.setState({b2: 0});
    this.setState({b3: 0});
    this.setState({b4: 0});
    this.setState({b5: 0});
  }


  render() {
    // const carname = {name: "Ford", model: "Mustang", quality: "Good"};
    // const carname = {name: "Ford", model: "Mustang", quality: "Good"};
    return (
      <div className="App">
        <h1>Basketball Foul Count</h1>
        <h2><label contenteditable="true">Team A</label></h2>
        <label contenteditable="true"><strong>Player A1</strong></label> <label>{this.state.a1}</label>&nbsp;&nbsp;
        {this.state.a1 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button onClick={this.a1increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.a1reset}>Reset</button>&nbsp;&nbsp;

        <label contenteditable="true"><strong>Player A2</strong></label> <label>{this.state.a2}</label>&nbsp;&nbsp;
        {this.state.a2 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.a2increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.a2reset}>Reset</button><br/><br/>

        <label contenteditable="true"><strong>Player A3</strong></label> <label>{this.state.a3}</label>&nbsp;&nbsp;
        {this.state.a3 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.a3increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.a3reset}>Reset</button>&nbsp;&nbsp;

        <label contenteditable="true"><strong>Player A4</strong></label> <label>{this.state.a4}</label>&nbsp;&nbsp;
        {this.state.a4 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.a4increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.a4reset}>Reset</button><br/><br/>

        <label contenteditable="true"><strong>Player A5</strong></label> <label>{this.state.a5}</label>&nbsp;&nbsp;
        {this.state.a5 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.a5increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.a5reset}>Reset</button><br/>
        <h3>Team A Total Foul(s): {this.state.a1 + this.state.a2 + this.state.a3 + this.state.a4 + this.state.a5}</h3>
        <button onClick={this.TeamAReset}>Reset Team A Foul(s)</button>


        <h2><label contenteditable="true">Team B</label></h2>
        <label contenteditable="true"><strong>Player B1</strong></label> <label>{this.state.b1}</label>&nbsp;&nbsp;
        {this.state.b1 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button onClick={this.b1increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.b1reset}>Reset</button>&nbsp;&nbsp;

        <label contenteditable="true"><strong>Player B2</strong></label> <label>{this.state.b2}</label>&nbsp;&nbsp;
        {this.state.b2 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.b2increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.b2reset}>Reset</button><br/><br/>

        <label contenteditable="true"><strong>Player B3</strong></label> <label>{this.state.b3}</label>&nbsp;&nbsp;
        {this.state.b3 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.b3increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.b3reset}>Reset</button>&nbsp;&nbsp;

        <label contenteditable="true"><strong>Player B4</strong></label> <label>{this.state.b4}</label>&nbsp;&nbsp;
        {this.state.b4 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.b4increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.b4reset}>Reset</button><br/><br/>

        <label contenteditable="true"><strong>Player B5</strong></label> <label>{this.state.b5}</label>&nbsp;&nbsp;
        {this.state.b5 === 5 ?  
        <button disabled className="btn btn-primary"
          type="button"
        >Foul++</button>
        : <button className="btn btn-primary"
          type="button"
          onClick={this.b5increment}
        >Foul++</button>}
        &nbsp;&nbsp;
        <button onClick={this.b5reset}>Reset</button><br/>
        <h3>Team B Total Foul(s): {this.state.b1 + this.state.b2 + this.state.b3 + this.state.b4 + this.state.b5}</h3>
        <button onClick={this.TeamBReset}>Reset Team B Foul(s)</button>


      </div>
    );
  }
}

export default App;